          <h6 style="text-transform: uppercase">nouvelle gamme capillaire</h6>
          <h2>Huile d'Argan & Grenade</h2>
          <h5 id="product_text_padding">
            Fabriquée en France, sans silicone ni sulfate, découvrez la gamme
            capillaire à l'huile d'Argan, réputée pour ses vertus protectrices,
