<script>
import Carousel from "@/components/Carousel.vue";
import CarouselSlide from "@/components/CarouselSlide.vue";
import PresentationProduit from "@/components/PresentationProduit.vue";
import Bonsplans from "@/components/Bonsplans.vue";
import Newsletter from "@/components/Newsletter.vue";
import Facilities from "@/components/Facilities.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "Home",
  components: {
    Carousel,
    CarouselSlide,
    PresentationProduit,
    Bonsplans,
    Newsletter,
    Facilities,
    Footer,
  },
  data() {
    return {
      slides: [
        {
          index: 0,
          introduction: " Du 23 au 29 Janvier",
          title: "L'hiver en douceur",
          paragraphe:
            "Duo Crème Mains & Baume lèvres, à l'Huile d'Olive, d'Argan ou Beurre de Karité.",
          button: "J'en profite",
          bg_img: require("@/assets/Carousel/fondorange.svg"),
          left_bg_img: require("@/assets/Carousel/bg_left_slideUn.svg"),
          img: require("@/assets/Carousel/image_slideUn.png"),
          title_bg_img: require("@/assets/Carousel/text_bg.png"),
        },
        {
          index: 1,
          introduction: "NOUVEAUTE",
          title: "Les écos-recharges",
          paragraphe:
            "Découvrez vos produits préférés dans un nouveau format Eco-Recharge 500ml, pour une économie de plastique* et une empreinte carbone réduite* ! ",
          button: "Je découvre",
          left_bg_img: require("@/assets/Carousel/bg_left_SlideDeux.jpeg"),
          bg_img: require("@/assets/Carousel/bg_right_slidedDeux.png"),
        },
        {
          index: 2,
          introduction: "NOUVEAUTE",
          title: "Cosmétique solide",
          paragraphe:
            "Découvrez le nouveau format Solide ! Un format eco-responsable, pratique à emporter partout avec soi et des formules simples avec un maximum de 12 ingrédients.",
          button: "Je les veux",
          bg_img: require("@/assets/Carousel/bg_right_slideTrois.jpeg"),
          left_bg_img: require("@/assets/Carousel/bg_left_slideTrois.svg"),
        },
      ],
      products: [
        {
          id: 0,
          img: require("../assets/produits/product1.png"),
        },
        {
          id: 1,
          img: require("@/assets/produits/product1.png"),
        },
      ],
      visibleSlide: 0,
      direction: "left",
    };
  },
  computed: {
    slideLeng() {
      return this.slides.length;
    },
  },
  methods: {
    next() {
      if (this.visibleSlide >= this.slideLeng - 1) {
        this.visibleSlide = 0;
      } else {
        this.visibleSlide++;
      }
      this.direction = "left";
    },
    prev() {
      if (this.visibleSlide <= 0) {
        this.visibleSlide = this.slideLeng - 1;
      } else {
        this.visibleSlide--;
      }
      this.direction = "right";
    },
  },
};
</script>
